html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
  line-height: 1;
  overflow-x: hidden;
}

.wraper {
  min-height: 100%;
  flex-direction: column;
  display: flex;
}

main {
  flex: auto;
}

*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

p, h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
}

input {
  outline: none;
}

img {
  margin: 0;
  display: block;
}

ul li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.header {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 40px;
  padding-bottom: 84px;
  display: block;
  position: relative;
}

@media screen and (max-width: 767px) {
  .header {
    padding-bottom: 94px;
  }
}

.library {
  padding-bottom: 166px;
}

@media screen and (min-width: 768px) {
  .library {
    padding-bottom: 142px;
  }
}

.nav {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

@media screen and (max-width: 767px) {
  .nav {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .nav {
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .nav {
    max-width: 1280px;
  }
}

.nav-logo {
  color: #fff;
  align-items: center;
  margin-right: 112px;
  font-size: 30px;
  font-weight: 500;
  display: flex;
}

.nav-logo:hover, .nav-logo:focus {
  color: #ff6b01;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.nav-logo:hover use, .nav-logo:focus use {
  stroke: #ff6b01;
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
}

.nav-logo__icon {
  width: 24px;
  height: 24px;
  fill: none;
  stroke: #fff;
}

@media screen and (min-width: 768px) {
  .nav-logo__icon {
    margin-right: 8px;
  }
}

@media screen and (min-width: 1280px) {
  .nav-logo__icon {
    margin-right: 10px;
  }
}

@media screen and (max-width: 767px) {
  .nav-logo__text {
    display: none;
  }
}

.header__background-home {
  background-color: #000;
  background-image: url("background-mobile.faab47c1.jpg");
}

@media screen and (min-width: 768px) {
  .header__background-home {
    background-color: #000;
    background-image: url("background-tab.b4b200b6.jpg");
  }
}

@media screen and (min-width: 1280px) {
  .header__background-home {
    background-color: #000;
    background-image: url("background-desc.ba0c274d.jpg");
  }
}

.header__background-library {
  background-color: #000;
  background-image: url("background-mobile-library.3bc30225.jpg");
}

@media screen and (min-width: 768px) {
  .header__background-library {
    background-color: #000;
    background-image: url("background-tab-library.de15ecda.jpg");
  }
}

@media screen and (min-width: 1280px) {
  .header__background-library {
    background-color: #000;
    background-image: url("background-desc-library.ad9d0683.jpg");
  }
}

.nav-buttons {
  display: flex;
}

.nav-btn {
  cursor: pointer;
  color: #fff;
  background: none;
  border: 0;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.17;
  display: block;
  position: relative;
}

.nav-btn:not(:last-child) {
  margin-right: 40px;
}

.nav-btn:hover, .nav-btn:focus {
  color: #ff6b01;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.current:after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: #ff001b;
  display: block;
  position: absolute;
  bottom: -5px;
}

.is-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.form {
  width: 280px;
  justify-content: space-between;
  align-items: center;
  margin-top: 54px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  position: relative;
}

@media screen and (min-width: 768px) {
  .form {
    width: 336px;
    margin-top: 40px;
  }
}

@media screen and (min-width: 1280px) {
  .form {
    width: 394px;
    margin-top: 40px;
  }
}

.form:hover, .form:focus {
  color: #ff6b01;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.form:hover:after, .form:focus:after {
  border-bottom: 1px solid #ff6b01;
  transition: border-bottom .25s cubic-bezier(.4, 0, .2, 1);
}

.form:hover ::-ms-input-placeholder, .form:focus ::-ms-input-placeholder {
  color: #ff6b01;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.form:hover ::placeholder, .form:focus ::placeholder {
  color: #ff6b01;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.form:hover .form-icon, .form:focus .form-icon {
  stroke: #ff6b01;
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
}

.form:after {
  content: "";
  width: 100%;
  height: 4px;
  background: none;
  border-bottom: 1px solid #fff;
  position: absolute;
  top: 100%;
  left: 0;
}

.form__input {
  color: #fff;
  width: 100%;
  background: none;
  border: none;
  outline: none;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
}

.form__input::-ms-input-placeholder {
  color: #fff;
}

.form__input::placeholder {
  color: #fff;
}

.form__btn-submit {
  object-fit: fill;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
}

.form-icon {
  width: 12px;
  height: 12px;
  fill: none;
  stroke: #fff;
}

.form-icon:hover {
  stroke: #ff6b01;
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
}

.form__error {
  width: 100%;
  text-align: left;
  color: #ff001b;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  position: absolute;
}

@media screen and (min-width: 768px) {
  .form__error {
    text-align: center;
    margin-top: 20px;
  }
}

.library-buttons {
  text-align: center;
  display: inline-flex;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media screen and (max-width: 767px) {
  .library-buttons {
    top: 65%;
  }
}

.btn {
  width: 130px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #fff;
  border-radius: 5px;
  padding-top: 14px;
  padding-bottom: 12px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  position: relative;
}

.btn:hover, .btn:focus {
  background-color: #ff6b01;
  border: 1px solid rgba(0, 0, 0, 0);
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), border .25s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 768px) {
  .btn {
    width: 152px;
  }
}

@media screen and (min-width: 1280px) {
  .btn {
    width: 148px;
    padding-top: 13px;
    padding-bottom: 11px;
  }
}

.btn:not(:last-child) {
  margin-right: 15px;
}

.current__library-button {
  background-color: #ff6b01;
  border: 1px solid rgba(0, 0, 0, 0);
}

.loader__container {
  width: 100%;
  height: 100%;
  z-index: 100;
  justify-content: center;
  align-items: center;
  transition: all .6s;
  display: flex;
  position: fixed;
  top: 0;
}

.loader {
  width: 150px;
  height: 150px;
  border: 15px solid #ff6b01;
  border-left-color: #a54301;
  border-radius: 50%;
  animation: loader 1.3s linear infinite;
}

@keyframes loader {
  100% {
    transform: rotate(360deg);
  }
}

.container {
  max-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
