.movie-grid-list {
  margin-left: auto;
  margin-right: auto;
  padding-top: 20px;
  padding-bottom: 40px;
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .movie-grid-list {
    grid-template-rows: auto;
