}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .grid-movie-card:hover {
    border-radius: 0 0 4px 4px;
    padding: 0 0 20px;
    position: absolute;
    transform: translate(0, -5px);
    box-shadow: 10px 8px 6px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 18px 10px 10px rgba(0, 0, 0, .2);
  }

  .grid-movie-card:hover .movie-info-wrapper {
    padding-top: 0;
    padding-left: 6px;
  }
}

@media screen and (min-width: 1280px) {
