    border-radius: 0 0 4px 4px;
    padding: 0 0 10px;
    transform: translate(0, -6px);
    box-shadow: 10px 8px 6px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 18px 10px 10px rgba(0, 0, 0, .2);
